/**
 * Vue Router
 *
 * @library
 *
 * https://router.vuejs.org/en/
 */

// Lib imports
import Vue from "vue";
import Router from "vue-router";
import Meta from "vue-meta";
import notfound from "./../notfound/notfound.vue";
import introduction from "./../introduction/introduction.vue";
import construction from "./../construction/construction.vue";
import education from "./../education/education.vue";
import stack from "./../stack/stack.vue";

Vue.use(Router);

// Create a new router
const router = new Router({
    mode: "history",
    routes: [
        {
            path: "/Home",
            name: "Home",
            component: introduction,
            meta: {
                showInRoutes: false,
                title: ""
            }
        },
        {
            path: "/",
            name: "Introduction",
            component: introduction,
            meta: {
                showInRoutes: false,
                title: "Introduction"
            }
        },
        {
            path: "/Stack",
            name: "Stack",
            component: stack,
            meta: {
                showInRoutes: true,
                title: "Technical Stack"
            }
        },
        {
            path: "/Experience",
            name: "Experience",
            component: construction,
            meta: {
                showInRoutes: true,
                title: "Professional Experience"
            }
        },
        {
            path: "/Education",
            name: "Education",
            component: education,
            meta: {
                showInRoutes: true,
                title: "Education Details"
            }
        },
        {
            path: "/Interests",
            name: "Interests",
            component: construction,
            meta: {
                showInRoutes: true,
                title: "Interests"
            }
        },
        {
            path: "/Contact",
            name: "Contact",
            component: construction,
            meta: {
                showInRoutes: true,
                title: "Contact"
            }
        },
        {
            path: "/NotFound",
            name: "NotFound",
            component: notfound,
            meta: {
                showInRoutes: false,
                title: ""
            }
        }
    ]
});

Vue.use(Meta);

router.beforeEach((to, from, next) => {
    if (to.matched.length) {
        next(); // make sure to always call next()!
    }
    else {
        next({ name: "NotFound" });
    }
});

export default router;